import React , { useState } from "react"
import { useStaticQuery , graphql } from "gatsby"
import Image from "gatsby-image"
import '../../styles/flickity.css'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';




export default ({css}) => {
    const data = useStaticQuery(graphql`
    query Certificates {
        Codecademy:file(relativePath: { eq: "Codecademy.jpg" }) {
         childImageSharp {
         fluid(maxWidth: 2000, quality: 100){
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    Udemy:file(relativePath: { eq: "udemy.jpg" }) {
         childImageSharp {
         fluid(maxWidth: 2000, quality: 100){
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    Go_it:file(relativePath: { eq: "go_it.jpeg" }) {
         childImageSharp {
         fluid(maxWidth: 2000, quality: 100){
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
    }
  `)

const [ photoIndex , seIndex ] = useState(0)
const [ isOpen , setOpen ] = useState(false)

  const images = [
    data.Go_it.childImageSharp.fluid.src,
    data.Codecademy.childImageSharp.fluid.src,
    data.Udemy.childImageSharp.fluid.src
  ];
 
    return(
        <section className = {css.certificates}>
            <h3>Certificates</h3>
      {isOpen && (
         <Lightbox
            mainSrc={images[photoIndex]}
            nextSrc={images[(photoIndex + 1) % images.length]}
            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
            onCloseRequest={() => setOpen(false)}
            onMovePrevRequest={() =>
                seIndex((photoIndex + images.length - 1) % images.length )
            }
            onMoveNextRequest={() =>
                seIndex((photoIndex + 1) % images.length )
            }
          />
        )}
     <div onClick = {() => { 
         setOpen(true)
         seIndex(0)
        }}>
     <Image  alt = 'go-it' fluid = {data.Go_it.childImageSharp.fluid} />
     </div>
     <div onClick = {() => { 
         setOpen(true)
         seIndex(1)
        }}>
     <Image alt = 'codecademy' fluid = {data.Codecademy.childImageSharp.fluid} />
     </div>
     <div onClick = {() => { 
         setOpen(true)
         seIndex(2)
        }}>
     <Image alt = 'udemy' fluid = {data.Udemy.childImageSharp.fluid} />
     </div>

    </section>
    )
}