import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Info from "../components/main_page/info"
import Skills from "../components/main_page/skills"
import Certificates from "../components/main_page/sertificates"
import css from "../styles/aboutMe.module.scss"
import META_IMAGE from '../images/anton.jpg'

export default () => (
  <Layout heading="ABOUT ME">
    <SEO image = {META_IMAGE} description = 'Full stack front end web developer portfolio , New York City , USA ' title="Front end web developer Anton Savytskyi" />
    <Info css={css} />
    <Skills css={css} />
    <Certificates css={css} />
  </Layout>
)
